<template>
  <div @click="filterchoice" :class="filter" class="filter-item" :style="`background-image:url(${imgUrl})`">
    <!-- {{ filter }} -->
    <slot></slot>
    <!-- <div @click="filterchoice">버튼</div> -->
  </div> 
</template>
<script>
export default {
  name: 'FilterBox',
  props: {
      imgUrl: String,
      filter: String
  },
  methods: {
    filterchoice(){
      this.emitter.emit('filterImg', this.filter);
    }
  }
}
</script>
<style>
.filter-item {
  width: 100px;
  height: 100px;
  margin: 10px 10px 10px auto;
  padding: 8px;
  display: inline-block;
  color : white;
  background-size: cover;
  background-position : center;
}  
</style>