<template>
  <div class="header">
    <ul class="header-button-left">
      <li>Cancel</li>
    </ul>
    <ul class="header-button-right">
      <li v-if="tab==1" @click="tab++">Next</li>
      <li v-if="tab==2" @click="publish()">발행</li>
    </ul>
    <img src="./assets/logo.png" class="logo" />
  </div>

  <p>{{ name }} {{ age }}</p>

  <!-- <h4>안녕 {{ $store.state.name }} {{ $store.state.age }}</h4>
  <button @click="$store.commit('addage', 10)">버튼</button>

  <p>{{ $store.state.more }}</p>
  <button @click="$store.dispatch('getData')">더보기</button> -->

  <ConTainer :list="list" :tab="tab" :imgUrl="imgUrl" @write="written=$event"/>
  <!-- <button @click="addlist()">더보기</button> -->

  <!-- <p>{{ now2 }} {{ count }}</p>
  <button @click="count++">버튼</button> -->

  <div class="footer">
    <ul class="footer-button-plus">
      <input type="file" @change="upload" id="file" class="inputfile" />
      <label for="file" class="input-plus">+</label>
    </ul>
 </div>

  <!-- <button @click="tab=0">버튼0</button>
  <button @click="tab=1">버튼1</button>
  <button @click="tab=2">버튼2</button> -->
</template>

<script>
import axios from 'axios';
import ConTainer from '@/components/ConTainer.vue';
import postdata from '@/assets/postdata';
import { mapMutations, mapState } from 'vuex';

export default {
  name: 'App',
  components: {
    ConTainer
  },
  data(){
    return {
      list: postdata,
      more: 0,
      tab: 0,
      imgUrl: '',
      written: '',
      today: '',
      choicefilter: '',
      count: 0,
    }
  },
  mounted() {
    this.emitter.on('filterImg', (a) => {
      console.log(a)
      this.choicefilter = a;
    })
  },
  computed: {
    ...mapState(['name', 'age'])
    // now2() {
    //   return new Date()
    // }
  },
  methods: {
    ...mapMutations(['addLikes', 'setMore']),
    // now() {
    //   return new Date()
    // },
    addlist: function(){
      axios.get(`https://jungwoonan.github.io/vue_data/more${this.more}.json`)
      .then( result => {
        this.list.push(result.data);
        this.more++;
        console.log(this.list)
      })
    },
    upload(e){
      let image = e.target.files;
      let url = URL.createObjectURL(image[0]);
      console.log(url);
      this.imgUrl = url;
      this.tab++;
    },
    publish(){
      let date = new Date();      
      let month = date.getMonth();
      let engmonth;
      console.log(month)
      switch(month){
        case 0 : engmonth='Jan'; break;
        case 1 : engmonth='Feb'; break;
        case 2 : engmonth='Mar'; break;
        case 3 : engmonth='Apr'; break;
        case 4 : engmonth='May'; break;
        case 5 : engmonth='Jun'; break;
        case 6 : engmonth='Jul'; break;
        case 7 : engmonth='Aug'; break;
        case 8 : engmonth='Sep'; break;
        case 9 : engmonth='Oct'; break;
        case 10 : engmonth='Nov'; break;
        case 11 : engmonth='Dec'; break;
      }
      console.log(engmonth)
      var day = new String(date.getDate());
      this.today = engmonth +' '+ day;

      var newpost = {
        name: "An jungwoon",
        userImage: "https://placeimg.com/100/100/arch",
        postImage: this.imgUrl,
        likes: 0,
        date: this.today,
        liked: false,
        content: this.written,
        filter: this.choicefilter
      };
      this.list.unshift(newpost);
      this.tab = 0;
    }
  }
}
</script>

