<template>
  <!-- 포스트페이지 -->
  <div v-if="tab==0">
    <PostList v-for="(item, i) in list" :key="i" :list="list[i]" :i="i"/>
  </div>
  
  <!-- 필터선택페이지 -->
  <div v-if="tab==1">
    <div :class="choicefilter" class="upload-image" :style="`background-image:url(${imgUrl})`"></div>
    <div class="filters">
        <FilterBox :imgUrl="imgUrl" :filter="filter" v-for="(filter, i) in filters" :key="i">{{filter}}</FilterBox>
    </div>
  </div>
  <!-- 글작성페이지 -->
  <div v-if="tab==2">
    <div :class="choicefilter" class="upload-image" :style="`background-image:url(${imgUrl})`"></div>
    <div class="write">
        <textarea @input="$emit('write', $event.target.value)" class="write-box" placeholder="write!"></textarea>
    </div>
  </div>
</template>
<script>
import PostList from './PostList.vue';
import FilterBox from './FilterBox.vue';

export default {
    name: 'ConTainer',
    components: {
        PostList,
        FilterBox
    },
    props: {
        list: Array,
        tab: Number,
        imgUrl: String
    },
    data(){
      return {
        filters: [ "aden", "_1977", "brannan", "brooklyn", "clarendon", "earlybird", "gingham", "hudson", 
"inkwell", "kelvin", "lark", "lofi", "maven", "mayfair", "moon", "nashville", "perpetua", 
"reyes", "rise", "slumber", "stinson", "toaster", "valencia", "walden", "willow", "xpro2"],
         choicefilter: ''
      }
    },
    mounted(){
      this.emitter.on('filterImg', (a)=>{
        this.choicefilter = a;
      })
    }    
}
</script>

<style>
.upload-image{
width: 100%;
height: 450px;
background: cornflowerblue;
background-size : cover;
}
.filters{
overflow-x:scroll;
white-space: nowrap;
}
.filter-1 {
width: 100px;
height: 100px;
background-color: cornflowerblue;
margin: 10px 10px 10px auto;
padding: 8px;
display: inline-block;
color : white;
background-size: cover;
}
.filters::-webkit-scrollbar {
height: 5px;
}
.filters::-webkit-scrollbar-track {
background: #f1f1f1; 
}
.filters::-webkit-scrollbar-thumb {
background: #888; 
border-radius: 5px;
}
.filters::-webkit-scrollbar-thumb:hover {
background: #555; 
}
.write-box {
border: none;
width: 90%;
height: 100px;
padding: 15px;
margin: auto;
display: block;
outline: none;
}</style>