import { createStore } from 'vuex'
import axios from 'axios';

const store = createStore({
    state() {
        return {
            name: 'kim',
            age: 20,
            likes: 30,
            liked: false,
            more: {},
        }
    },
    mutations: {
        setMore(state, payload) {
            state.more = payload
        },
        addLikes(state) {
            if (state.liked == false) {
                state.likes++;
                state.liked = true;
            }else {
                state.likes--
                state.liked = false;
            }            
        },
        // addage(state, payload) {
        //     state.age += payload
        // },
        // nameChange(state) {
        //     state.name = 'park'
        // }        
    },
    actions: {
        getData(context) {
            axios.get('https://jungwoonan.github.io/vue_data/more0.json')
                .then((result) => {
                    console.log(result.data)
                    context.commit('setMore', result.data)
            })
        }
    }
})

export default store